@use 'base/variables' as v;
@use 'base/mixins' as m;

.pagination {
    padding: v.$separation 0; // Seperation 5 top and bottom, 0 left and right

    @include m.phone {
        display: flex;
        justify-content: space-between;
    }
    button {
        margin: 0 0 1rem 0;
    }
}
.hidden {
    visibility: hidden;
}