@use 'base/variables' as v;

.alert-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .2rem;
}

.alert {
    padding: calc(v.$separation / 4);
    color: v.$white;
    width: auto;
    display: block;
    margin-bottom: 1rem;
    text-align: center;
    border-radius: 5px;
    font-weight: v.$bold;
    &.error {
        background-color: v.$red;
        border-color: #f5c6cb;
    }
    &.success {
        background-color: v.$green;
    }
}
