@use 'base/variables' as v;
@use 'base/mixins' as m;

.container-app {
    @include m.desktop {
        display: grid;
        grid-template-columns: repeat(2, 40% 60%);
        min-height: 100vh;
    }
    .image {
        height: 20rem;
        width: 100%;
        overflow: hidden;
        position: relative;
    
        @include m.desktop {
            height: auto;
        }
        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            object-position: center;
        }
    }
    .app {
        width: 100%;
        margin: 0 auto;
        padding: 3rem 2rem;

        @include m.desktop {
            padding: 3rem 3rem;
            overflow-y: scroll;
        }
        
    }
}
.bar {
    margin: v.$separation 0;
    @include m.desktop {
        display: flex;
        justify-content: space-between;
        align-items: center;
        p {
            padding-left: 3.4rem;
        }
    }
    .button {
        @include m.button(v.$black, v.$white);
        margin: 0;
    }
    p {
        text-align: center;
    } 
}

.services-bar {
    margin-bottom: v.$separation;

    @include m.desktop {
        display: flex;
        justify-content: space-between;
    }
    .button {
        margin: 0 0 1rem 0;
    }
}