@use 'base/variables' as v;

.summary-content {
    p {
        margin-bottom: 1rem;
    }
    span {
        color: v.$blue;
        font-weight: v.$bold;
        font-size: 2.4rem;
    }
}
.container-service {
    padding: 3rem 0;
    border-bottom: 1px solid v.$grey;

    &:last-of-type {
        margin-bottom: v.$separation;
    }
}